
html {
  font-size: 62.5%;
  height: 100%;
}
body {
  margin: 0;
  height: 100%;
  background: #efefef;
}
p {
  margin: 0;
}
ul{
  list-style:none;
}
#app{
  height: 100%;
}

#main{
  max-width:600px;
  width: 100%;
  min-height: 100%;
  height: auto;
  margin: 0 auto;
  margin-top: -10px;
  background-color: white;
}
#main_body{
  width: 100%;
  height: auto;
  margin-bottom: 60px;
}
#main_body_ul{
  padding: 0px;
}
#main_footer{
position: fixed;
bottom: 0;
left: 0;
width: 100%;
height: 60px;
}
#main_footer_form{
  max-width: 600px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  border: 1px solid #ddd;
  background: white;
}
#main_footer_form_text{
  width: 85%;
  height: 30px;
  resize: none;
  border: none;
  outline: none;
  margin-top: 20px;
}
#main_footer_form_submit{
  border-style: none;
  width: 10%;
  height: 100%;
  float: right;
}
.main_body_message{
  height: auto;
  display: inline-block;
  width: 100%;
}
.main_body_message_li{
  min-height: 20px;
  height: 100%;
  margin-bottom: 5px;
  margin-left: 10px;
  float: left;
  max-width: 400px;
  width: 100%;
}
.main_body_my_message_li{
  min-height: 20px;
  height: 100%;
  margin-bottom: 5px;
  margin-right: 10px;
  float: right;
}
.main_body_login_message_li{
  min-height: 20px;
  height: 100%;
  margin-bottom: 5px;
  width: 40%;
  margin: auto;
  text-align: center;
}
.user{
  color: #888;
}
.message{
  float: left;
  color: #444;
  font-size: 16px;
  background-color: #ddd;
  border-radius: 5px;
  width: auto;
  max-width: 350px;
  word-wrap: break-word;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.login{
    float: left;
    font-size: 16px;
    background-color: #999;
    border-radius: 5px;
    width: 90%;
    word-wrap: break-word;
    color: white;
}
.time{
  float: left;
  margin-left: 5px;
}
.my_message{
  max-width: 350px;
  width: auto;
  float: right;
  color: #444;
  font-size: 16px;
  background-color: #7ec699;
  border-radius: 5px;
  margin-top: 10px;
  word-wrap: break-word;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.my_time{
  float: right;
  margin-right: 5px;
}





































